.bottom-sidebar__left,
.bottom-sidebar__right {
  background: linear-gradient(to bottom, #ffffff 0%, #f5f5f5 50%, #eeeeee 100%);
}

* {
  box-sizing: border-box;
}

html {
  font-family: verdana, arial, sans-serif;
  font-size: 12px;
}

body {
  display: flex;
  flex-direction: column;
  background: url("../img/background.jpg");
  background-color: #3a3a3a;
  background-position: 50% 0;
  align-items: center;
}

a {
  text-decoration: none;
}

h2 {
  margin: 10px 0;
  color: #0b81be;
  font-size: 2rem;
  font-weight: normal;
}

h3 {
  font-size: 2rem;
  font-weight: normal;
}

p {
  color: #858585;
  line-height: 1.75;
}

i {
  color: #424242;
}

li {
  line-height: 1.5;
}

.wrapper {
  max-width: 850px;
  padding: 0 15px;
  flex-grow: 1;
}

.header {
  display: flex;
  flex: 1 0 100%;
  margin: 34px 0;
  align-items: center;
}

.header-logo {
  flex: 0 0 200px;
}

.menu {
  position: relative;
  z-index: 9;
  display: flex;
  justify-content: flex-end;
}

.menu__list {
  display: flex;
  min-height: 40px;
  margin: 0;
  padding: 0;
  list-style: none;
  align-items: center;
}

.menu__list.active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.menu__list.active .menu__list-item {
  width: 100%;
  margin: 0;
  text-align: center;
}

.menu__list.active .menu__list-item a {
  width: 100%;
  padding: 15px;
  border-radius: 0;
}

.menu__list-item {
  display: block;
  margin-left: 7px;
}

.menu__list-item a {
  display: block;
  padding: 12px 15px;
  text-decoration: none;
  text-transform: uppercase;
  color: #efefef;
  border-radius: 15px;
}

.menu__list-item:hover:not(.active) a {
  background-color: rgba(255, 255, 255, 0.2);
}

.menu__list-item.active a {
  background: linear-gradient(to bottom, #0f8bbe 0%, #2989d8 50%, #207cca 51%, #0eb7fe 100%);
}

.menu__btn {
  flex-direction: column;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.25);
  align-items: center;
  justify-content: center;
}

.menu__btn-row {
  position: relative;
  width: 60%;
  height: 2px;
  margin: 2px 0;
  transition: all 0.5s;
  background-color: #ffffff;
}

.menu__btn.active .menu__btn-row:first-of-type {
  top: 6px;
  transform: rotate(-135deg);
}

.menu__btn.active .menu__btn-row:last-of-type {
  top: -6px;
  transform: rotate(-225deg);
}

.menu__btn.active .menu__btn-row:nth-of-type(2) {
  opacity: 0;
}

.container {
  display: flex;
  margin-bottom: 50px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  flex-wrap: wrap;
}

.main {
  background-color: #efefef;
}

.main p {
  text-align: justify;
}

.carousel {
  position: relative;
  width: 100%;
  padding: 15px;
  background-color: #efefef;
  order: 1;
}

.carousel-slides {
  position: relative;
}

.carousel-slide {
  position: absolute;
  top: 0;
  display: flex;
  transition: opacity 0.5s;
  opacity: 0;
  border: 7px solid #ffffff;
  background-image: url(../img/carousel-item-bg.png);
  background-position: 50% 50%;
  background-size: cover;
  box-shadow: 0 0 1px #dddddd;
}

.carousel-slide.active {
  position: relative;
  z-index: 1;
  opacity: 1;
}

.carousel-slide__image {
  display: flex;
  padding: 15px;
  align-items: center;
}

.carousel-slide__image img {
  max-width: 100%;
}

.carousel-slide__text h3 {
  text-transform: uppercase;
  color: #ffffff;
}

.carousel-slide__text p {
  color: #ffffff;
  display: -webkit-box;
  overflow: hidden;
  max-height: 70px;
  font-size: 1rem;
  line-height: 1.5;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
}

.carousel-control {
  position: absolute;
  z-index: 2;
  top: 50%;
}

.carousel-control img {
  margin-top: -15px;
}

.carousel-control.prev {
  left: 3px;
}

.carousel-control.next {
  right: 3px;
  transform: scale(-1, 1);
}

.left-sidebar {
  position: relative;
  background-color: #efefef;
}

.left-sidebar ul {
  padding-left: 20px;
}

.left-sidebar li {
  position: relative;
  padding: 7.5px;
  list-style: none;
  color: #858585;
  font-style: italic;
}

.left-sidebar li::before {
  position: absolute;
  margin-left: -15px;
  content: url("../img/main-list-arrow.png");
}

.left-sidebar li span {
  position: relative;
}

.left-sidebar li span::after {
  position: absolute;
  right: 0;
  bottom: -10px;
  left: 0;
  height: 1px;
  content: "";
  background-color: #dddddd;
}

.bottom-sidebar {
  position: relative;
  display: flex;
  flex: 1 0 100%;
  padding: 15px;
  background-color: #efefef;
  flex-wrap: wrap;
  order: 4;
}

.bottom-sidebar::before {
  position: absolute;
  top: 14px;
  right: 14px;
  bottom: 14px;
  left: 14px;
  content: "";
  background-color: #dddddd;
}

.bottom-sidebar p {
  margin: 0;
  color: #858585;
  line-height: 1.5em;
}

.bottom-sidebar img {
  padding: 0 15px 15px 0;
}

.bottom-sidebar__left {
  position: relative;
  display: flex;
  border: 5px solid #ffffff;
  align-items: flex-start;
}

.bottom-sidebar__right {
  position: relative;
  display: flex;
  border: 5px solid #ffffff;
  align-items: flex-start;
}

.footer {
  display: flex;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  justify-content: center;
}

.footer .wrapper {
  display: flex;
  margin-top: 37px;
}

.footer .menu__btn {
  display: none;
}

.footer .menu__list-item {
  font-size: 0.9rem;
}

.footer-logo {
  margin: 15px;
}

.twitter {
  display: flex;
  flex-wrap: wrap;
}

.twitter__image {
  flex: 0 0 55px;
}

.twitter__description {
  flex: 1 0 100px;
}

.twitter__description-title {
  display: block;
  color: #ffffff;
  font-size: 2rem;
}

.twitter__description-slogan {
  color: #727272;
}

.twitter__post {
  position: relative;
  flex: 1 0 calc(100% - 67px);
  padding: 20px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.4);
}

.twitter__post::before {
  position: absolute;
  top: -19px;
  left: 0;
  content: url("../img/twitter-speech-bubble.png");
}

.twitter__post-text {
  color: #858585;
  line-height: 18px;
}

.twitter__post-date {
  color: #0b81be;
  font-size: 0.83rem;
  font-style: italic;
}

.copyright {
  color: #ffffff;
}

@media (min-width: 577px) {
  .twitter__post {
    margin: 12px 30px 30px 37px;
  }
}

@media (min-width: 769px) {
  .menu {
    flex: 1 0 200px;
  }

  .main {
    flex: 1 0 calc(100% - 300px);
    padding: 0 20px 15px 15px;
    order: 3;
  }

  .carousel-slide__text {
    padding: 15px 30px 15px 15px;
    flex-grow: 1;
  }

  .carousel-slide__text h3 {
    margin: 15px 0;
    flex-grow: 1;
  }

  .carousel-slide__text p {
    margin-bottom: 15px;
    text-align: justify;
    flex-grow: 1;
  }

  .left-sidebar {
    flex: 0 0 300px;
    padding: 0 15px 15px 20px;
    order: 2;
  }

  .left-sidebar::after {
    position: absolute;
    top: 15px;
    right: 0;
    height: 90%;
    content: "";
    border-right: 1px solid #dddddd;
  }

  .bottom-sidebar__left {
    flex: 1 0 50%;
    padding: 20px 15px 25px;
    border-right: none;
  }

  .bottom-sidebar__left::after {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    content: "";
    border-right: 1px solid #dddddd;
  }

  .bottom-sidebar__right {
    flex: 1 0 50%;
    padding: 20px 15px 25px;
    border-left: none;
  }

  .footer-logo {
    text-align: right;
  }
}

@media (min-width: 851px) {
  .menu__btn {
    display: none;
  }

  .carousel-slide__image {
    flex: 0 0 250px;
  }

  .copyright {
    margin: 57px 15px 15px 15px;
    text-align: right;
  }
}

@media (max-width: 850px) {
  .menu__list {
    position: absolute;
    z-index: -999;
    top: 100%;
    right: -15px;
    visibility: hidden;
    flex-direction: column;
    width: 100vw;
    transition: opacity 0.5s;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.8);
  }

  .menu__btn {
    display: flex;
  }

  .footer .menu__list {
    top: initial;
    bottom: 100%;
  }
}

@media (max-width: 768px) {
  .menu {
    flex: 1 0 50px;
  }

  .main {
    flex: 1 0 100%;
    padding: 20px;
    order: 2;
  }

  .carousel-slide {
    flex-wrap: wrap;
  }

  .carousel-slide__image {
    flex: 1 0 100%;
    justify-content: center;
  }

  .carousel-slide__text {
    flex: 1 0 100%;
    padding: 0 15px 15px 15px;
  }

  .carousel-slide__text h3 {
    overflow: hidden;
    flex: 1 0 100%;
    max-width: calc(100vw - 135px);
    margin: 0 0 15px 0;
    text-align: center;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .carousel-slide__text p {
    margin: 0 0 15px 0;
    margin-bottom: 0;
    text-align: center;
  }

  .left-sidebar {
    flex: 1 0 100%;
    padding: 0 20px 20px 20px;
    order: 3;
  }

  .bottom-sidebar__left {
    flex: 1 0 100%;
    padding: 15px;
  }

  .bottom-sidebar__right {
    flex: 1 0 100%;
    padding: 15px;
  }

  .bottom-sidebar__right p {
    order: 1;
  }

  .bottom-sidebar__right img {
    padding: 0 0 15px 15px;
    order: 2;
  }

  .footer-left {
    flex: 1 0 100%;
  }

  .footer-right {
    flex: 1 0 100%;
  }

  .footer .wrapper {
    flex-wrap: wrap;
  }

  .footer-logo {
    text-align: center;
  }

  .copyright {
    margin: 30px 15px 15px 15px;
    text-align: center;
  }
}

@media (max-width: 576px) {
  .footer .menu {
    justify-content: center;
  }

  .twitter__post {
    margin: 12px 0 30px 37px;
  }
}

@media (max-width: 850px) and (min-width: 769px) {
  .carousel-slide__image {
    flex: 0 0 220px;
  }

  .footer-left {
    flex: 0 0 60%;
  }

  .footer-right {
    flex: 0 0 40%;
  }

  .copyright {
    margin: 75px 15px 15px 15px;
    text-align: right;
  }
}